import React from "react";
import { Link } from "react-router-dom";
import Nav from "./components/nav";

class Add extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      first_name: "",
      last_name: "",
      address: "",
      phone: "",
      email: "",
      isGuest: false,
      error: false
    };
  }
  handleChange = e => {
    const value =
      e.target.type === "checkbox" ? e.target.checked : e.target.value;
    this.setState({ [e.target.name]: value });
  };

  handleSubmit = e => {
    if (
      this.state.first_name === "" ||
      this.state.last_name === "" ||
      this.state.address === "" ||
      this.state.phone_home === ""
    ) {
      this.setState({ error: true });
    } else if (this.state.isGuest) {
      fetch("/members/createguest", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({ guest: this.state }),
        credentials: "include"
      })
        .then(r => r.text())
        .then(text => {
          console.log(text);

          this.props.history.push("/classinfo");
        });
    } else {
      fetch("/members/create", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({ member: this.state }),
        credentials: "include"
      })
        .then(r => r.text())
        .then(text => {
          console.log(text);

          this.props.history.push("/classinfo");
        });
    }

    e.preventDefault();
  };

  render() {
    return (
      <div className="container form-page">
        <Nav dest="/classinfo" edit=""></Nav>
        <p className="title">Add a new member</p>
        {/* <div className="letter">
          <span>N</span>
        </div> */}
        {this.state.error ? (
          <p className="error">Please fill out the required fields.</p>
        ) : null}
        <form className="form-grid">
          <div className="floating-label-wrapper">
            <input
              name="first_name"
              onChange={this.handleChange}
              placeholder="First Name"
              type="text"
              className="floating-label-field"
            ></input>
            <label htmlFor="first_name" className="floating-label">
              First Name
            </label>
          </div>
          <div className="floating-label-wrapper">
            <input
              name="last_name"
              onChange={this.handleChange}
              placeholder="Last Name"
              type="text"
              className="floating-label-field"
            ></input>
            <label htmlFor="last_name" className="floating-label">
              Last Name
            </label>
          </div>
          <div className="floating-label-wrapper">
            <input
              name="address"
              onChange={this.handleChange}
              placeholder="Address"
              type="text"
              className="floating-label-field"
            ></input>
            <label htmlFor="address" className="floating-label">
              Address
            </label>
          </div>
          <div className="floating-label-wrapper">
            <input
              name="phone"
              onChange={this.handleChange}
              placeholder="phone"
              type="text"
              className="floating-label-field"
            ></input>
            <label htmlFor="phone" className="floating-label">
              Phone
            </label>
          </div>
          <div className="floating-label-wrapper">
            <input
              name="email"
              onChange={this.handleChange}
              placeholder="Email"
              type="email"
              className="floating-label-field"
            ></input>
            <label htmlFor="email" className="floating-label">
              Email
            </label>
          </div>
          <div>
            <label>
              <input
                type="checkbox"
                checked={this.state.isGuest}
                onChange={this.handleChange}
                name="isGuest"
              ></input>
              Guest
            </label>
          </div>

          <Link to="/addexisting">
            <button className="button button-large">add existing member</button>
          </Link>
          <button className="button button-large" onClick={this.handleSubmit}>
            submit
          </button>
        </form>
      </div>
    );
  }
}

export default Add;
