import React from "react";
import Nav from "./components/nav";

class AddExisting extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      members: [],
      active: [],
      filter: "",
      error: false
    };
  }

  handleSubmit = e => {
    e.preventDefault();
    console.log(this.state.active.length);

    if (this.state.active.length <= 0) {
      alert("Please select at least one memeber");
    } else {
      fetch("/class/membership", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({ members: this.state.active }),
        credentials: "include"
      }).then(() => {
        this.props.history.push("/classinfo");
      });
    }
  };

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  toggleAdd = id => {
    const hAct = this.state.active;

    //to determine if it's hightlighted
    if (hAct.includes(id)) hAct.splice(hAct.indexOf(id), 1);
    else hAct.push(id);

    this.setState({ active: hAct });
  };

  componentDidMount = () => {
    fetch("/class/noclass", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      credentials: "include"
    })
      .then(r => r.json())
      .then(data => {
        this.setState({ members: data.Members });
      });
  };

  render() {
    const { filter } = this.state;
    return (
      <div className="container">
        <Nav dest="/classinfo"></Nav>
        <div className="attendance-grid">
          <div className="attendance-head add-existing expand">
            <p className="title">Existing members</p>
            <p className="item-name">Highlight members to be added</p>
            <input
              type="text"
              name="filter"
              placeholder="Search..."
              className="text-input"
              onChange={this.handleChange}
            ></input>
          </div>
          <div className="expand">
            <ul>
              {this.state.members
                ? this.state.members
                    .filter(member => {
                      if (
                        member.first_name
                          .toLowerCase()
                          .includes(filter.toLowerCase()) ||
                        member.last_name
                          .toLowerCase()
                          .includes(filter.toLowerCase()) ||
                        member.address
                          .toLowerCase()
                          .includes(filter.toLowerCase())
                      )
                        return member;
                      return null;
                    })
                    .map(member => {
                      var mi = "";
                      if (member.first_name)
                        mi =
                          member.first_name.toLowerCase().substring(0, 1) +
                          member.last_name.toLowerCase().substring(0, 1);
                      else mi = "AA";
                      return (
                        <li
                          key={member.id}
                          className={
                            this.state.active.includes(member.id)
                              ? "selected list-item attendance"
                              : "list-item attendance"
                          }
                          onClick={this.toggleAdd.bind(this, member.id)}
                        >
                          <div className="list-item-initials">
                            <span>{mi}</span>
                          </div>
                          <span className="item-name">
                            {member.first_name} {member.last_name}
                          </span>
                          <span className="item-info">{member.address}</span>
                        </li>
                      );
                    })
                : null}
            </ul>
          </div>
          <button
            className="button button-large expand"
            onClick={this.handleSubmit}
          >
            submit
          </button>
        </div>
      </div>
    );
  }
}

export default AddExisting;
