import React from "react";
import Nav from "./nav";

const Attendance = props => (
  <div className="container">
    <Nav dest="/classinfo" edit=""></Nav>
    <div className="attendance-grid">
      <div className="attendance-head expand">
        <p className="title">Attendance</p>
        <p className="item-name">Highlight the members who are present</p>
      </div>
      <div className="expand">
        <ul>
          {props.members
            ? props.members
                .filter(member => {
                  return member.status !== "missing_accounted";
                })
                .map(member => {
                  var mi = "";
                  if (member.first_name)
                    mi =
                      member.first_name.substring(0, 1) +
                      member.last_name.substring(0, 1);
                  else mi = "AA";
                  return (
                    <li
                      key={member.id}
                      className={
                        props.active.includes(member.id)
                          ? "selected list-item attendance"
                          : "list-item attendance"
                      }
                      onClick={props.toggleAttendance.bind(this, member.id)}
                    >
                      <div className="list-item-initials">
                        <span>{mi}</span>
                      </div>
                      <span className="item-name">
                        {member.first_name} {member.last_name}
                      </span>
                      <span className="item-info">{member.address}</span>
                    </li>
                  );
                })
            : null}
        </ul>
      </div>
      <button className="next button" onClick={props.nextStep}>
        NEXT
      </button>
    </div>
  </div>
);

export default Attendance;
