import React from "react";
import Attendance from "./components/attendance";
import Missing from "./components/missing";
import Study from "./components/study";

class Mark extends React.Component {
  state = {
    step: 1,
    attn: [],
    mssn: [],
    stdy: [],
    overwrite: false,
    members: [
      {
        id: "1",
        name: "Andrew Aardvark",
        address: "Vernon's Estate",
        status: "absent",
        studied: 0
      },
      {
        id: "2",
        name: "Brandon Marks",
        address: "Vernon's Estate",
        status: "absent",
        studied: 0
      },
      {
        id: "3",
        name: "Charles King",
        address: "Vernon's Estate",
        status: "absent",
        studied: 0
      }
    ]
  };

  loadData = () => {
    fetch("/class/members", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      credentials: "include"
    })
      .then(r => r.json())
      .then(data => {
        data.forEach(member => {
          member["status"] = "absent";
          member["studied"] = 0;
        });
        this.setState({ members: data });
        console.log(this.state.members);
      });
  };

  componentDidMount = () => {
    fetch("/class/checkattendance")
      .then(r => r.json())
      .then(r => {
        if (r.length > 0) alert("records already exist for today");
        else this.loadData();
      });
    this.loadData();
  };

  toggleAttendance = id => {
    const temp = this.state.members;
    const hAct = this.state.attn;

    //to determine if it's hightlighted
    if (hAct.includes(id)) hAct.splice(hAct.indexOf(id), 1);
    else hAct.push(id);

    temp.forEach(member => {
      if (member.id === id) {
        if (member.status === "absent") member.status = "present";
        else member.status = "absent";
      }
    });
    this.setState({ members: temp, attn: hAct });
  };

  toggleMssnAccounted = id => {
    const temp = this.state.members;
    const hAct = this.state.mssn;
    if (hAct.includes(id)) hAct.splice(hAct.indexOf(id), 1);
    else hAct.push(id);
    temp.forEach(member => {
      if (member.id === id) {
        if (member.status === "absent") member.status = "missing_accounted";
        else member.status = "absent";
      }
    });
    this.setState({ members: temp, mssn: hAct });
  };

  toggleStudied = id => {
    const temp = this.state.members;
    const hAct = this.state.stdy;
    if (hAct.includes(id)) hAct.splice(hAct.indexOf(id), 1);
    else hAct.push(id);
    temp.forEach(member => {
      if (member.id === id) {
        if (member.studied === 0) member.studied = 1;
        else member.studied = 0;
      }
    });
    this.setState({ members: temp, stdy: hAct });
  };

  handleSubmit = event => {
    event.preventDefault();
    fetch("/class/addrecords", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        members: this.state.members,
        overwrite: this.state.overwrite
      }),
      credentials: "include"
    }).then(() => {
      this.props.history.push("/classinfo");
      console.log("sent");
    });
    console.log("stuff");
  };

  nextStep = () => {
    const { step } = this.state;
    this.setState({
      step: step + 1
    });
  };

  prevStep = () => {
    const { step } = this.state;
    this.setState({
      step: step - 1
    });
  };
  render() {
    const { step } = this.state;

    switch (step) {
      case 1:
        return (
          <Attendance
            active={this.state.attn}
            members={this.state.members}
            toggleAttendance={this.toggleAttendance}
            nextStep={this.nextStep}
          />
        );
      case 2:
        return (
          <Missing
            active={this.state.mssn}
            toggleMssnAccounted={this.toggleMssnAccounted}
            members={this.state.members}
            nextStep={this.nextStep}
            prevStep={this.prevStep}
          />
        );
      case 3:
        return (
          <Study
            active={this.state.stdy}
            done={this.handleSubmit}
            toggleStudied={this.toggleStudied}
            members={this.state.members}
            nextStep={this.nextStep}
            prevStep={this.prevStep}
          />
        );
      default:
        return <Attendance nextStep={this.nextStep} />;
    }
  }
}

export default Mark;
