import React from "react";
import "./css/App.css";
import Login from "./Login";
import Mark from "./Mark";
import Add from "./Add";
import AddExisting from "./AddExisting";
import ClassInfo from "./ClassInfo";
import Member from "./Member";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import Cookies from "universal-cookie";

const cookies = new Cookies();

const PublicRoute = ({ component: Component, ...rest }) => {
  return (
    <Route
      {...rest}
      render={routeProps =>
        !cookies.get("blitz") ? (
          <Component {...routeProps} />
        ) : (
          <Redirect
            to={{
              pathname: "/classinfo",
              state: { from: routeProps.location }
            }}
          />
        )
      }
    />
  );
};
const PrivateRoute = ({ component: Component, ...rest }) => {
  return (
    <Route
      {...rest}
      render={routeProps =>
        cookies.get("blitz") ? (
          <Component {...routeProps} />
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: routeProps.location }
            }}
          />
        )
      }
    />
  );
};

const Lost = () => {
  return <p>There doesn't seem to be anything here...</p>;
};

function App() {
  return (
    <Router>
      <Switch>
        <PublicRoute path="/login" component={Login} />
        <PrivateRoute exact path="/" component={ClassInfo} />
        <PrivateRoute path="/classinfo" component={ClassInfo} />
        <PrivateRoute path="/mark" component={Mark} />
        <PrivateRoute path="/member/:id" component={Member} />
        <PrivateRoute path="/guest/:id" component={Member} />
        <PrivateRoute path="/addmember" component={Add} />
        <PrivateRoute path="/addexisting" component={AddExisting} />
        <PrivateRoute component={Lost} />
      </Switch>
    </Router>
  );
}

export default App;
