import React from "react";
import fetch from "unfetch";
import logo from "./img/ssdb.svg";

class Login extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      username: "",
      password: "",
      err: false
    };
  }

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value, err: false });
  };

  handleSubmit = e => {
    fetch("/auth/login", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(this.state),
      credentials: "include"
    }).then(r => {
      if (r.status === 200) this.props.history.push("/classinfo");
      // if (r.status === 200) alert("success");
      else if (r.status === 422) this.setState({ err: true });
    });
    e.preventDefault();
  };

  render() {
    return (
      <div className="login-page container">
        <form className="form-grid">
          <img className="logo" src={logo} alt="ssdb logo"></img>
          <div className="floating-label-wrapper">
            {this.state.err ? (
              <span className="error">Incorrect username or password</span>
            ) : null}
            <input
              onChange={this.handleChange}
              name="username"
              className="floating-label-field"
              placeholder="Username"
              type="text"
            ></input>
            <label htmlFor="username" className="floating-label">
              Username
            </label>
          </div>
          <div className="floating-label-wrapper">
            <input
              onChange={this.handleChange}
              name="password"
              className="floating-label-field"
              placeholder="Password"
              type="password"
            ></input>
            <label htmlFor="password" className="floating-label">
              Password
            </label>
          </div>
          <button
            onClick={this.handleSubmit}
            className="button button-large"
            type="submit"
          >
            Login
          </button>
        </form>
      </div>
    );
  }
}

export default Login;
