import React from "react";
import { Link } from "react-router-dom";
import Nav from "./components/nav";
import list from "./img/file.svg";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";

class ClassInfo extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      classinfo: {
        name: "",
        division: "",
        Teacher: {},
        Secretary: {},
        Care_Coordinator: {},
        Members: [],
        Guests: []
      },
      filter: ""
    };
  }

  logout = () => {
    fetch("/auth/logout").then(() => {
      this.props.history.push("/");
    });
  };

  componentDidMount = () => {
    fetch("/class/myclassinfo", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      credentials: "include"
    })
      .then(r => r.json())
      .then(data => {
        this.setState({
          classinfo: data
        });
      });
  };
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  render() {
    const { classinfo, filter } = this.state;

    return (
      <div className="container profile-page">
        <div className="class-info">
          <Nav dest="menu" logout={this.logout}></Nav>
          <div className=" page-head">
            <div className="letter expand">
              <span> {classinfo.name.substring(0, 1).toUpperCase()} </span>
            </div>
            <div className="expand">
              <p className="title"> Class {classinfo.name} </p>
              <p className="item-info">
                {classinfo.division} division |{" "}
                <span> {classinfo.Members.length} </span> members
              </p>
            </div>
            <div className="leader">
              <p className="item-name">
                {classinfo.Teacher.first_name} {classinfo.Teacher.last_name}
              </p>
              <p className="item-info"> Teacher </p>
            </div>
            <div className="leader">
              <p className="item-name">
                {classinfo.Secretary.first_name} {classinfo.Secretary.last_name}
              </p>
              <p className="item-info"> Secretary </p>
            </div>
            <div className="leader">
              <p className="item-name">
                {classinfo.Care_Coordinator.first_name}{" "}
                {classinfo.Care_Coordinator.last_name}
              </p>
              <p className="item-info"> Care Coordinator </p>
            </div>
            <Link to="/addmember">
              <button className="button-inverse button-small">
                add member
              </button>
            </Link>
            <Link to="/report">
              <button className="button-inverse button-small"> report </button>
            </Link>
            <Link to="/mark">
              <button className="button-inverse button-small">
                attendance
              </button>
            </Link>
            <span className="splash"> class </span>
          </div>
        </div>
        <div className="item-list">
          <input
            className="search"
            type="text"
            name="filter"
            placeholder="Search..."
            onChange={this.handleChange}
          ></input>
          <Tabs>
            <TabList>
              <Tab>Members</Tab>
              <Tab>Guests</Tab>
            </TabList>

            <TabPanel>
              {classinfo.Members.length > 0 ? (
                <ul>
                  {classinfo.Members.filter(member => {
                    if (
                      member.first_name
                        .toLowerCase()
                        .includes(filter.toLowerCase()) ||
                      member.last_name
                        .toLowerCase()
                        .includes(filter.toLowerCase()) ||
                      member.address
                        .toLowerCase()
                        .includes(filter.toLowerCase())
                    )
                      return member;
                    return null;
                  }).map(member => {
                    var mi = "";
                    if (member.first_name)
                      mi =
                        member.first_name.substring(0, 1) +
                        member.last_name.substring(0, 1);
                    else mi = "AA";
                    return (
                      <Link key={member.id} to={`/member/${member.id}`}>
                        <li className="list-item">
                          <div className="list-item-initials">
                            <span> {mi.toLowerCase()} </span>
                          </div>
                          <span className="list-item-name">
                            {member.first_name} {member.last_name}
                          </span>
                          <span className="list-item-info">
                            {member.address}
                          </span>
                        </li>
                      </Link>
                    );
                  })}
                </ul>
              ) : (
                <div className="empty-list">
                  <img src={list} alt="empty list"></img>
                  <p> No members... </p>
                </div>
              )}
            </TabPanel>
            <TabPanel>
              {classinfo.Guests.length > 0 ? (
                <ul>
                  {classinfo.Guests.filter(guest => {
                    if (
                      guest.first_name
                        .toLowerCase()
                        .includes(filter.toLowerCase()) ||
                      guest.last_name
                        .toLowerCase()
                        .includes(filter.toLowerCase()) ||
                      guest.address.toLowerCase().includes(filter.toLowerCase())
                    )
                      return guest;
                    return null;
                  }).map(guest => {
                    var mi = "";
                    if (guest.first_name)
                      mi =
                        guest.first_name.substring(0, 1) +
                        guest.last_name.substring(0, 1);
                    else mi = "AA";
                    return (
                      <Link key={guest.id} to={`/guest/${guest.id}`}>
                        <li className="list-item">
                          <div className="list-item-initials">
                            <span> {mi.toLowerCase()} </span>
                          </div>
                          <span className="list-item-name">
                            {guest.first_name} {guest.last_name}
                          </span>
                          <span className="list-item-info">
                            {guest.address}
                          </span>
                        </li>
                      </Link>
                    );
                  })}
                </ul>
              ) : (
                <div className="empty-list">
                  <img src={list} alt="empty list"></img>
                  <p> No guests... </p>
                </div>
              )}
            </TabPanel>
          </Tabs>
        </div>
      </div>
    );
  }
}

export default ClassInfo;
