import React from "react";
import Nav from "./nav";

const Missing = props => (
  <div className="container">
    <Nav dest="/classinfo" edit=""></Nav>
    <div className="attendance-grid">
      <div className="attendance-head expand">
        <p className="title">Missing</p>
        <p className="item-name">Highlight the members who are accounted for</p>
      </div>
      <div className="expand">
        <ul>
          {props.members

            .filter(member => {
              return member.status !== "present";
            })
            .map(member => {
              var mi = "";
              if (member.first_name)
                mi =
                  member.first_name.substring(0, 1) +
                  member.last_name.substring(0, 1);
              else mi = "AA";
              return (
                <li
                  key={member.id}
                  className={
                    props.active.includes(member.id)
                      ? "missing-selected list-item missing"
                      : "list-item missing"
                  }
                  onClick={props.toggleMssnAccounted.bind(this, member.id)}
                >
                  <div className="list-item-initials">
                    <span>{mi}</span>
                  </div>
                  <span className="item-name">
                    {member.first_name} {member.last_name}
                  </span>
                  <span className="item-info">{member.address}</span>
                </li>
              );
            })}
        </ul>
      </div>
      <button className="prev button" onClick={props.prevStep}>
        PREV
      </button>
      <button className="next button" onClick={props.nextStep}>
        NEXT
      </button>
    </div>
  </div>
);

export default Missing;
