import React from "react";
import Nav from "./components/nav";
import list from "./img/file.svg";
import Modal from "./Modal";

class Member extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      memberinfo: {
        first_name: "Andrew",
        last_name: "Aardvark",
        address: "Vernon's Estate",
        phone_home: "(268)000-0000",
        email: "aardark@nothing.com",
        Attendances: []
      },
      show: false,
      err: false,
      guest: false
    };
  }

  showModal = () => {
    this.setState({ show: true });
  };

  hideModal = () => {
    this.setState({ show: false, err: false });
  };

  componentDidMount = () => {
    let reqUrl = "/members/read";
    if (this.props.match.path.includes("guest")) {
      reqUrl = "/guests/read";
      this.setState({ guest: true });
    }
    fetch(reqUrl, {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({ id: this.props.match.params.id }),
      credentials: "include"
    })
      .then(r => r.json())
      .then(data => {
        console.log(data);
        this.setState({ memberinfo: data });
      });
  };

  remove = () => {
    fetch("/members/delete", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({ id: this.props.match.params.id }),
      credentials: "include"
    }).then(r => {
      if (r.status === 200) this.props.history.push("/classinfo");
      // if (r.status === 200) alert("success");
      else if (r.status === 422) this.setState({ err: true });
    });
  };

  getMonth = date => {
    switch (date) {
      case 0:
        return "January";
      case 1:
        return "February";
      case 2:
        return "March";
      case 3:
        return "April";
      case 4:
        return "May";
      case 5:
        return "June";
      case 6:
        return "July";
      case 7:
        return "August";
      case 8:
        return "September";
      case 9:
        return "October";
      case 10:
        return "November";
      case 11:
        return "December";
      default:
        return "Month";
    }
  };
  getDay = date => {
    switch (date) {
      case 0:
        return "Sunday";
      case 1:
        return "Monday";
      case 2:
        return "Tuesday";
      case 3:
        return "Wednesday";
      case 4:
        return "Thursday";
      case 5:
        return "Friday";
      case 6:
        return "Saturday";
      default:
        return "Day";
    }
  };

  render() {
    const { memberinfo } = this.state;

    return (
      <div className="member container profile-page">
        <div className="member-head">
          <Nav dest="/classinfo" color="white"></Nav>
          <div className="page-head">
            <div className="letter expand">
              <span>{memberinfo.first_name.substring(0, 1)}</span>
            </div>
            <p className="item-name expand">
              {memberinfo.first_name} {memberinfo.last_name}
            </p>
            <p className="item-info expand">
              {memberinfo.address}
              <br />
              Phone: {memberinfo.phone} Email: {memberinfo.email}
            </p>
            {/* <Link to="/classinfo">
            <button className="button-inverse button-small">class info</button>
          </Link> */}
            <span className="splash">member</span>
          </div>
        </div>
        <div className="item-list">
          <p className="sub-title">Attendance</p>
          {memberinfo.Attendances.length > 0 ? (
            <ul className="record-list">
              {memberinfo.Attendances.map(record => {
                var dateFormatted = new Date(record.createdAt);
                var day = this.getDay(dateFormatted.getDay());
                var month = this.getMonth(dateFormatted.getMonth());
                var year = dateFormatted.getFullYear();
                return (
                  <li
                    key={record.id}
                    className={
                      record.study ? "list-item study-selected" : "list-item"
                    }
                  >
                    <span className="date expand">
                      {day} {dateFormatted.getDate()}, {month} {year}
                    </span>
                    <span className="did-study expand">{record.status}</span>
                  </li>
                );
              })}
            </ul>
          ) : (
            <div className="empty-list">
              <img src={list} alt="empty list"></img>
              <p>No records...</p>
            </div>
          )}
        </div>
        {this.state.guest ? null : (
          <button
            className="button button-large error"
            onClick={this.showModal}
          >
            REMOVE
          </button>
        )}
        <Modal show={this.state.show}>
          <div className="modal-grid">
            <p className="expand">
              Are you sure you want to remove this member from this class?
            </p>
            <button className="button button-small" onClick={this.remove}>
              YES
            </button>
            <button className="button button-small" onClick={this.hideModal}>
              NO
            </button>
          </div>
        </Modal>
        <Modal show={this.state.err}>
          <div className="modal-grid">
            <p className="expand">Cannot remove while logged in...</p>
            <button className="button button-small" onClick={this.hideModal}>
              OK
            </button>
          </div>
        </Modal>
      </div>
    );
  }
}

export default Member;
